.searchResult {
    @include respond(tabLand) {
        padding: 3rem;
        width: 100%;
        flex-basis: 60%;
    }

    @include respond(phone) {
        padding: 3rem;
        flex-basis: 100%;
    }

    &__content {
        padding: 1rem 1rem;
        margin: 0 auto;
        border-radius: 1rem;
        color: $color-grey-light-1;
        width: 95%;
        background-color: rgba($color-grey-light-2, 0.3);
    }

    &__table {
        border-collapse: collapse;
        font-size: 1.4rem;
        transition: all 0.2s;

        @include respond(phone) {
            font-weight: 400;
        }

        &--wrapper {
            column-count: 2;

            @include respond(tinyPhone) {
                column-count: 1;
            }
        }

        td {
            padding: 0.25rem 1rem;
            white-space: nowrap;
        }

        td:nth-child(even) {
            @include respond(phone) {
                padding-left: 1rem;
            }
        }

        td:nth-child(odd) {
            text-align: right;
            border-right: 1px solid $color-grey-dark-1;

            @include respond(phone) {
                padding-right: 1rem;
            }
        }
    }
}

#coleader {
    border-bottom: 4px solid $color-coleader;
    border-top: 4px solid $color-coleader;
    @include respond(tabLand) {
        border-top: none;
    }
}

